.dropzone, .dropzoneActive {
  width: 100%;
  border-radius: 5px;
  border-width: 2px;
  border-color: #ccc;
  cursor: pointer;
  border-style: dashed;
  transition-property: all;
  transition-duration: 0.3s;
  position: relative;
  user-select: none;
}

.dropzone:hover, .dropzoneActive:hover {
  border-color: #2db7f5;
}

.dropzoneActive {
  border-style: solid;
  border-color: #2db7f5;
}

.imageViewBase {
  width: 100%;
}

.uploading {
  position: relative;
  background-color: #F5F6F9;
  width: 100%;
}

.uploading p {
  text-align: center;
  width: 100%;
  position: relative;
}

.uploading .cameraIcon {
  text-align: center;
  position: relative;
}

.backgroundCover {
  background-size: cover;
  background-position: center;
  height: 400px;
  background-repeat: no-repeat;
  border-radius: 0px;
}

@media screen and (min-width: 768px) {
  .backgroundCover {
    border-radius: 7px 7px 0 0;
  }
}

@media screen and (max-width: 768px) {
  .backgroundCover {
    height: 300px;
  }
}

.deleteButton {
  margin-top: 15px;
  float: right;
  border: 1px solid;
  border-radius: 2px;
  box-sizing: content-box;
  color: white;
  margin-right: 20px;
  border-color: white;
  background-color: rgba(0, 0, 0, 0.6);
  font-size: 13px;
  cursor: pointer;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
}

.backgroundCoverForComment {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 5px;
  height: 45px;
  margin-top: -3px;
}

.deleteForCommentImage {
  position: relative;
  background-color: rgba(0, 0, 0, 0.2);
  width: 10px;
  height: 10px;
  float: right;
  top: -5px;
  right: -5px;
  border-radius: 50%;
}

.backgroundCoverForAvatar {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  height: 100px;
}



