.controls {
  height: 36px;
  width: 100%;
  line-height: 36px;
  font-size: 109%;
  text-align: left;
  direction: ltr;
  position: absolute;
  bottom: 5px;
  margin: 0;
  padding: 0;
  border: 0;
  background-color: black;
}

.controls button {
  opacity: 0.9;
  display: inline-block;
  transition: opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
  overflow: hidden;
  margin: 0px;
  padding: 0px;
  border: 0px;
  height: 100%;
  cursor: pointer;
  background: transparent;
}

.controls input {
  display: inline-block;
  transition: opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
  overflow: hidden;
  height: 100%;
  padding: 0px;
  border: 0px;
  margin: 0px;
}

.leftControls {
  float: left;
  height: 100%;
  width: calc(100% - 190px);
}

@media (max-width: 768px) {
  .leftControls {
    width: calc(100% - 40px);
  }
}

.rightControls {
  display: block;
  float: right;
  height: 100%;
}

@media (max-width: 768px) {
  .rightControls button:first-child {
    display: none !important;
  }
}

.timeDispaly {
  font-size: 13px;
  display: inline-block;
  vertical-align: top;
  padding: 0 5px;
  white-space: nowrap;
  line-height: 35px;
}

.timeDispaly span {
  border: 0px;
  padding: 0px;
  margin: 0px;
  font-size: 100%;
  background: transparent;
  color: #ddd;
}

.seekBar {
  width: calc(100% - 160px) !important;
}

@media (max-width: 768px) {
  .seekBar {
    width: calc(100% - 125px) !important;
  }
}

.volume {
  width: 80px !important;
}

@media (max-width: 768px) {
  .volume {
    display: none !important;
  }
}



